import React, { useState } from "react";
import axios from "axios";
import { Formik } from "formik";
import * as Yup from "yup";

import { StyledForm, Label, Input, MessageInput, StyledMessage, Button } from "./style"

const formSchema = Yup.object().shape({
  name: Yup.string().required("Required"),
  email: Yup.string()
    .email("Invalid email")
    .required("Required"),
  message: Yup.string().required("Required")
});

export default () => {
  /* Server State Handling */
  const [serverState, setServerState] = useState();
  const handleServerResponse = (ok, msg) => {
    setServerState({ok, msg});
  };
  const handleOnSubmit = (values, actions) => {
    axios({
      method: "POST",
      url: "https://getform.io/f/95fc2a97-dd9b-4c98-a908-dcc53d767328",
      data: values
    })
      .then(response => {
        actions.setSubmitting(false);
        actions.resetForm();
        handleServerResponse(true, "Sent, Thanks!");
      })
      .catch(error => {
        actions.setSubmitting(false);
        handleServerResponse(false, error.response.data.error);
      });
  };
  return (
    <div>
      <Formik
        initialValues={{ name: "", email: "", message: "" }}
        onSubmit={handleOnSubmit}
        validationSchema={formSchema}
      >
        {({ isSubmitting }) => (
          <StyledForm id="fs-frm" noValidate>
            <Label htmlFor="name">Name:</Label>
            <Input id="name" type="name" name="name" />
            <StyledMessage name="name" className="errorMsg" component="p" />
            <Label htmlFor="email">Email:</Label>
            <Input id="email" type="email" name="email" />
            <StyledMessage name="email" className="errorMsg" component="p" />
            <Label htmlFor="message">Message:</Label>
            <MessageInput id="message" name="message" component="textarea" />
            <StyledMessage name="message" className="errorMsg" component="p" />
            <Button type="submit" disabled={isSubmitting}>
              Submit
            </Button>
            {serverState && (
              <StyledMessage className={!serverState.ok ? "errorMsg" : ""}>
                {serverState.msg}
              </StyledMessage>
            )}
          </StyledForm>
        )}
      </Formik>
    </div>
  );
};